import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';

const signUpSchema = Yup.object().shape({
    name: Yup.string()
            .required('  ').default(''),
    login: Yup.string()
            .required('  ').default(''),
    password: Yup.string()
            .min(6, '    6 ')
            .max(20, '    20 ')
            .required('  ').default(''),
});

const formSignUpOptions = {resolver: yupResolver(signUpSchema)};

export default formSignUpOptions;