import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';

const signInSchema = Yup.object().shape({
    login: Yup.string()
            .required('  ').default(''),
    password: Yup.string()
             .min(6, '    6 ')
             .max(20, '    20 ')
             .required('  ').default(''),
});

const formSignInOptions = {resolver: yupResolver(signInSchema)};

export default formSignInOptions;