import {useContext, useState, useEffect} from 'react';
import { ProductContext } from '../../contexts/ProductContext.js';
import { useForm } from "react-hook-form";
import formBuyOptions from "../../utils/validSchemas/buySchema";
import './Cart.scss';

function Cart(props) {

    const [isSuccess, setIsSuccess] = useState(false);

    function onSubmit(e) {
        setIsSuccess(true);
    }

    const [isEmpty, setIsEmpty] = useState(true);

    const {products} = useContext(ProductContext);
    const { register, handleSubmit, formState: { errors } } = useForm(formBuyOptions);

    function productsPriceSum() {
        let sum = 0
        products.forEach(product => {
            sum += product.price;
        });
        return sum;
    }


    useEffect(() => {
        products.length !== 0 && setIsEmpty(false);
    }, [])
    

    return (
        <div className="container">
        <div className="cart">
            <h2 className="cart__title"></h2>
            { !isEmpty ?
            <>
            <p className="cart__text"> {products.length}   {productsPriceSum()} .</p>
            {products.map(item => {
                return (
                    <div className="cart__item" key={item.id}>
                        <img src={item.img} alt="Image" className="cart__item-img" />
                        <p className="cart__item-name">{item.name}</p>
                        <p className="cart__item-desc">{item.shortDescription}</p>
                        <p className="cart__item-price">{item.price} .</p>
                    </div>
                )
            })
        }
        </>
            :
            <p className="cart__text"> </p>
}
        </div>
        { isSuccess ? <h2 className="success-message"> !</h2>
        :
        <form className={`pay-form ${isEmpty && "hidden"}`} noValidate onSubmit={handleSubmit(onSubmit)}>
        <h2 className="pay-form__title"></h2>
        <label htmlFor="name" className="signup__label"> 
          <input
            {...register('name')}
            type="text" 
            name="name"
            id="name"
            placeholder="  "
            className="signup__input" />
          <span className="signup__input-error">{errors.name?.message }</span>
          </label>
          <label htmlFor="email" className="signup__label"> E-mail
          <input
            {...register('email')}
            type="text" 
            name="email"
            id="email"
            placeholder="  " 
            className="signup__input" />
            <span className="signup__input-error">{errors.email?.message }</span>
          </label>

          <p className="pay-form__oplata-title"> </p>

          <label className="pay-form__label-radio"> 
          <input
            {...register('oplata')}
            type="radio" 
            name="oplata"
            id="oplata1" 
            className="pay-form__input-radio" />
            <span className='pay-form__checkmark'></span>
          </label>
          <label className="pay-form__label-radio"> Qiwi
          <input
            {...register('oplata')}
            type="radio" 
            name="oplata"
            id="oplata2" 
            className="pay-form__input-radio" />
            <span className='pay-form__checkmark'></span>
          </label>
          <label className="pay-form__label-radio"> PayPal
          <input
            {...register('oplata')}
            type="radio" 
            name="oplata"
            id="oplata3" 
            className="pay-form__input-radio" />
            <span className='pay-form__checkmark'></span>
          </label>
          <span className="pay-form__input-error">{errors.oplata?.message }</span>
          <button type='submit' onSubmit={onSubmit} className="pay-form__btn"></button>
        </form>
}
        </div>
    )
}

export default Cart;